/* REST vars */
.policy-container {
  min-height: 100vh;
  width: 50%;
  display: flex;
  gap: 20px;
  flex-direction: column;
  margin: 4rem auto;
}
.policy-container h1 {
  color: #f1702d;
}

.policy-text {
  display: flex;
  gap: 10px;
  flex-direction: column;
}
.policy-text h2 {
  margin: 1rem auto;
  color: #ff9861;
}
.policy-text p {
  font-size: 12px;
  line-height: 1.7;
}