/* REST vars */
.advantages-container {
  position: relative;
  width: 60%;
  margin: 0 auto;
  margin: 8rem auto 0;
  display: flex;
  gap: 80px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.advantages-container h1 {
  text-align: center;
  font-weight: 400;
  font-family: "Rubik Gemstones", cursive;
  color: #ffa574;
  filter: drop-shadow(3px 3px #222222);
  font-size: 40px;
  text-transform: uppercase;
  line-height: 1.1111111111;
  letter-spacing: 1px;
  margin: 0 auto;
}
.advantages-container h2 {
  font-weight: 400;
  font-size: 18px;
  letter-spacing: 1px;
  line-height: 2.2;
}
.advantages-container h2 span {
  font-weight: 800;
}
.advantages-container .advantage-container {
  margin: 0 auto;
  display: grid;
  gap: 60px;
  grid-template-columns: 1fr 1fr 1fr;
}
.advantages-container .advantage-container .advantage {
  display: grid;
  gap: 30px;
  grid-template-rows: 0.5fr 1fr;
  place-items: center;
  font-weight: 400;
  background-color: transparent;
  transition: 0.5s;
  text-align: center;
  cursor: pointer;
}
.advantages-container .advantage-container .advantage:hover {
  transform: scale(0.95);
}
.advantages-container .advantage-container .advantage h2 {
  font-size: 26px;
  font-weight: 100;
  font-family: "Rubik Gemstones", cursive;
  line-height: 1.3333333333;
  letter-spacing: 2px;
  color: #ffa574;
  filter: drop-shadow(2px 2px #222222);
  text-transform: uppercase;
}
.advantages-container .advantage-container .advantage p {
  font-size: 16px;
  line-height: 2;
}

.advantages-images {
  position: absolute;
  z-index: -3;
  opacity: 0.3;
  color: #dda7bb;
  font-size: 100px;
}

.advantages-planet-image {
  top: 15%;
  right: -12%;
}

.advantages-orange-image {
  bottom: 13%;
  left: 75%;
  transform: rotate(-10deg);
}

.advantages-line-image {
  top: -15%;
  left: 40%;
}

.advantages-eye-image {
  bottom: 5%;
  left: 8%;
  transform: rotate(10deg);
}

.advantages-currency-image {
  top: 30%;
  left: -25%;
  transform: rotate(-18deg);
}

@media (max-width: 1680px) {
  .advantages-container {
    gap: 50px;
  }
  .advantages-container h1 {
    font-size: 35px;
  }
  .advantages-container h2 {
    font-size: 14px;
  }
  .advantages-container .advantage-container {
    gap: 40px;
  }
  .advantages-container .advantage-container .advantage {
    gap: 20px;
  }
  .advantages-container .advantage-container .advantage h2 {
    font-size: 20px;
  }
  .advantages-container .advantage-container .advantage p {
    font-size: 14px;
  }
}
@media (max-width: 1500px) {
  .advantages-container h1 {
    width: 70%;
    line-height: 1.6;
    filter: drop-shadow(2px 2px #f1702d);
  }
}
@media (max-width: 1180px) {
  .advantages-container {
    width: 65%;
    gap: 40px;
  }
  .advantages-container h1 {
    width: 80%;
  }
}
@media (max-width: 1080px) {
  p {
    font-size: 14px;
  }
}
@media (max-width: 990px) {
  .advantages-container {
    width: 70%;
  }
  .advantages-container h1 {
    width: 80%;
    font-size: 30px;
  }
  .advantages-container h2 {
    width: 95%;
  }
  .advantages-container .advantage-container {
    display: flex;
    gap: 60px;
    flex-direction: column;
  }
  .advantages-container .advantage-container .advantage h2 {
    width: 70%;
    text-align: center;
    padding: 0.25rem;
  }
}
@media (max-width: 770px) {
  .advantages-container h1 {
    width: 100%;
    filter: drop-shadow(1.5px 1.5px #f1702d);
  }
  .advantages-container .advantage-container .advantage h2 {
    font-size: 20px;
  }
  .advantages-container .advantage-container .advantage p {
    font-size: 13px;
  }
  .advantages-images {
    font-size: 80px;
  }
}
@media (max-width: 600px) {
  .advantages-container {
    width: 90%;
    margin: 0 auto;
  }
}
@media (max-width: 480px) {
  .advantages-container h2 {
    font-size: 14px;
    line-height: 2;
  }
  .advantages-container .advantage-container .advantage {
    grid-template-rows: 0.25fr 0.75fr;
  }
  .advantages-container .advantage-container p {
    font-size: 12px;
    letter-spacing: 0.8px;
  }
}