/* REST vars */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 10px;
  scroll-behavior: smooth;
  overflow-x: hidden;
  background-color: #fffbf8;
  color: #222222;
  font-family: "Comfortaa", sans-serif;
  font-weight: 300;
}

.basic-btn {
  padding: 0.5rem;
  text-transform: uppercase;
  font-size: 25px;
  font-family: "Bitter", sans-serif;
  letter-spacing: 4px;
  font-weight: 900;
  color: #fefcfd;
  border: none;
  border-radius: 25px;
  background-color: #dda7bb;
  filter: drop-shadow(4px 4px #a8476a);
  transition: 0.5s;
  cursor: pointer;
}
.basic-btn p {
  filter: drop-shadow(1px 1px #222222);
}
.basic-btn:hover {
  transform: scale(0.9);
  color: #fffbf8;
  background-color: #a8476a;
  filter: none;
}

.basic-btn:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

#btn-up {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: fixed;
  top: 85%;
  right: 3%;
  z-index: 100;
  width: 50px;
  height: 50px;
  border: none;
  border-radius: 25px;
  background-color: transparent;
  transition: 0.5s ease-out;
  overflow: hidden;
  cursor: pointer;
}
#btn-up:hover {
  opacity: 1;
  transform: scale(0.7);
}
#btn-up:hover .btn-up-icon {
  color: #dda7bb;
  filter: drop-shadow(1px 1px #a8476a);
}
#btn-up .btn-up-icon {
  font-size: 40px;
  color: #ff9861;
  filter: drop-shadow(1px 1px #f1702d);
  transform: rotate(-45deg);
}

.slider-container {
  width: 100%;
  display: grid;
  grid-template-columns: 0.25fr 5.5fr 0.25fr;
  place-content: center;
  gap: 5px;
}
.slider-container .toggle-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  filter: none;
  color: #dda7bb;
  font-size: 40px;
  background-color: transparent;
  filter: drop-shadow(2px 2px #a8476a);
  animation: scale 2s infinite;
  cursor: pointer;
}

.toast-error-message,
.toast-success-message {
  width: 98%;
  padding: 1.5rem;
  color: #222222;
  font-weight: 800;
  background-color: #fffbf8;
  font-family: "Comfortaa", sans-serif;
  font-size: 12px;
  letter-spacing: 0.5px;
  line-height: 1.8;
  border-radius: 60px;
}

.success-bar {
  background-color: #302939;
}

.error-bar {
  background-color: #dda7bb;
}

.square {
  position: absolute;
  border-radius: 25px;
}

.big {
  width: 200vw;
  height: 200vw;
  z-index: -5;
  transform: rotate(20deg);
}

.medium {
  width: 200vw;
  height: 50vh;
  z-index: -3;
  transform: rotate(175deg);
}

.small-1 {
  position: absolute;
  border-radius: 25px;
  width: 95px;
  height: 95px;
  transform: rotate(-80deg);
  animation: pulsatingSquare1 3s infinite;
}

.small-2 {
  width: 120px;
  height: 120px;
  transform: rotate(30deg);
  animation: pulsatingSquare2 3s infinite;
}

.small-3 {
  width: 65px;
  height: 65px;
  animation: pulsatingSquare3 4s infinite;
}

.small-4 {
  width: 80px;
  height: 80px;
  transform: rotate(20deg);
  animation: pulsatingSquare2 3s infinite;
}

@keyframes pulsating {
  0% {
    margin-top: 0;
  }
  50% {
    margin-top: 1rem;
    transform: scale(0.8);
  }
  100% {
    margin-top: 0;
    transform: scale(1);
  }
}
@keyframes scale {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes pulsatingSquare1 {
  0% {
    transform: rotate(20deg) scale(1);
  }
  50% {
    transform: rotate(20deg) scale(0.95);
  }
  100% {
    transform: rotate(20deg) scale(1);
  }
}
@keyframes pulsatingSquare2 {
  0% {
    transform: rotate(-5deg) scale(1);
  }
  50% {
    transform: rotate(-25deg) scale(0.85);
  }
  100% {
    transform: rotate(-5deg) scale(1);
  }
}
@keyframes pulsatingSquare3 {
  0% {
    transform: rotate(-35deg) scale(1);
  }
  50% {
    transform: rotate(-65deg) scale(0.9);
  }
  100% {
    transform: rotate(-35deg) scale(1);
  }
}
@media (max-width: 1540px) {
  .small-2 {
    width: 100px;
    height: 100px;
  }
  .small-1 {
    width: 75px;
    height: 75px;
  }
}
@media (max-width: 1540px) {
  #btn-up {
    right: 1%;
  }
}
@media (max-width: 875px) {
  .small-1,
  .small-2,
  .small-3,
  .small-4 {
    display: none;
  }
}
@media (max-width: 890px) {
  #btn-up {
    width: 45px;
    height: 45px;
  }
  #btn-up .btn-up-icon {
    font-size: 35px;
  }
}
@media (max-width: 745px) {
  #btn-up {
    display: none;
  }
}
@media (max-width: 610px) {
  .slider-container {
    width: 90%;
    margin: 0 auto;
  }
}
@media (max-width: 550px) {
  .slider-container {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  .slider-container .toggle-btn {
    font-size: 20px;
  }
}