/* REST vars */
.form-container {
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.contact-form {
  width: 50%;
  display: flex;
  gap: 40px;
  flex-direction: column;
  margin: 8rem auto;
}
.contact-form h1 {
  width: 95%;
  font-weight: 100;
  color: #302939;
  font-size: 55px;
  font-family: "Rubik Gemstones", cursive;
  text-align: center;
  letter-spacing: 1px;
  line-height: 1.8;
  margin: 0 auto;
}
.contact-form h1 span {
  color: #dda7bb;
  filter: drop-shadow(2px 2px #a8476a);
}
.contact-form h2 {
  margin: 0 auto;
  width: 80%;
  font-size: 30px;
  font-weight: 100;
  font-family: "Bitter", sans-serif;
  text-align: center;
  letter-spacing: 1px;
  line-height: 1.6;
}
.contact-form h2 span {
  color: #ffd8c4;
  filter: drop-shadow(2px 2px #f1702d);
}
.contact-form h3 {
  width: 40%;
  margin: 2rem auto 0;
  font-weight: 100;
  font-size: 18px;
  line-height: 2;
  text-align: center;
}
.contact-form h3 .email-icon {
  margin-top: 1rem;
  width: 100%;
  z-index: 100;
  text-align: center;
  font-size: 55px;
  color: #ffd8c4;
  filter: drop-shadow(3px 3px #f1702d);
  transition: 0.5s;
  cursor: pointer;
}
.contact-form h3 .email-icon:hover {
  color: #dda7bb;
  filter: drop-shadow(2px 2px #a8476a);
  transform: scale(0.9);
}
.contact-form .application-btn {
  width: 30%;
  margin: 0 auto;
  font-size: 20px;
}

.questions {
  display: flex;
  gap: 20px;
  flex-direction: column;
}
.questions .question {
  width: 100%;
  display: flex;
  gap: 8px;
  flex-direction: column;
}

.checkbox-container {
  display: flex;
  gap: 20px;
  align-items: center;
  cursor: pointer;
  font-size: 16px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin: 0.75rem;
}
.checkbox-container input {
  position: absolute;
  opacity: 0;
  height: 0;
  width: 0;
  cursor: pointer;
}
.checkbox-container p {
  width: 95%;
}

.checkmark {
  height: 20px;
  width: 20px;
  background-color: #ffd8c4;
  border-radius: 25px;
}

.checkbox-container input:checked ~ .checkmark {
  background-color: #f1702d;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.checkbox-container input:checked ~ .checkmark:after {
  display: block;
}

.checkbox-container .checkmark:after {
  top: 50%;
  left: 50%;
  width: 10px;
  height: 10px;
  transform: translate(-50%, -50%);
  border-radius: 25px;
  background: #fffbf8;
}

label {
  color: #222222;
  font-size: 16px;
  line-height: 2;
}

input,
textarea {
  width: 100%;
  padding: 0.7rem;
  border: none;
  font-family: inherit;
  border-radius: 25px;
  background-color: #fefcfd;
  box-shadow: 1px 1px 4px #f1702d;
  filter: drop-shadow(2px 2px #ffd8c4);
  outline: none;
}

textarea {
  height: 15vh;
  padding: 1rem;
}

input::placeholder,
textarea::placeholder {
  opacity: 0.8;
}

.contact-images {
  position: absolute;
  font-size: 120px;
  color: #dda7bb;
  opacity: 0.4;
  z-index: -3;
}

.contact-phone-image {
  top: 125%;
  left: 50%;
  font-size: 80px;
}

.contact-star-image {
  top: 50%;
  left: 15%;
}

.contact-rainbow-image {
  top: 155%;
  right: 15%;
}

.contact-rocket-image {
  top: 70%;
  right: 15%;
}

.contact-pin-image {
  top: 20%;
  left: 50%;
  font-size: 60px;
}

.contact-clip-image {
  top: 185%;
  left: 15%;
  font-size: 60px;
}

@media (max-width: 1650px) {
  .contact-form {
    width: 60%;
  }
  .checkbox-container {
    width: 80%;
  }
}
@media (max-width: 1540px) {
  .contact-form h2 {
    width: 90%;
  }
}
@media (max-width: 1380px) {
  .contact-form {
    width: 70%;
  }
  .contact-form h1 {
    width: 80%;
    margin: 0 auto;
    font-size: 38px;
  }
  .contact-form h2 {
    width: 70%;
    font-size: 25px;
  }
  .contact-form h2 span {
    filter: drop-shadow(1px 1px #f1702d);
  }
}
@media (max-width: 1040px) {
  .contact-form h2 {
    font-size: 25px;
  }
}
@media (max-width: 950px) {
  label {
    font-size: 14px;
  }
  .checkbox-container {
    font-size: 14px;
  }
  .contact-plane-image {
    width: 20%;
    top: 130%;
  }
}
@media (max-width: 800px) {
  .contact-form {
    width: 80%;
    margin: 6rem auto;
  }
  .contact-form h1 {
    font-size: 30px;
  }
  .contact-form h2 {
    font-size: 18px;
  }
  .contact-form h3 {
    width: 50%;
    font-size: 14px;
  }
  .contact-form .application-btn {
    width: 40%;
  }
  .checkmark {
    height: 15px;
    width: 15px;
  }
  label {
    color: #222222;
    font-size: 12px;
  }
  .checkbox-container {
    font-size: 12px;
  }
  .checkbox-container p {
    font-size: 12px;
  }
  .contact-images {
    font-size: 60px;
  }
}
@media (max-width: 670px) {
  .contact-form h1 {
    font-size: 28px;
  }
  .contact-form h2 {
    font-size: 20px;
  }
}
@media (max-width: 600px) {
  .contact-form {
    width: 90%;
  }
  .contact-form h1 {
    width: 85%;
    margin: 0 auto;
    line-height: 1.4;
    font-size: 26px;
  }
  .contact-form h2 {
    width: 80%;
    line-height: 1.8;
    font-size: 20px;
  }
  .contact-form h3 {
    width: 55%;
  }
  .contact-form .application-btn {
    width: 50%;
  }
  .questions .question {
    margin-left: 2rem;
  }
  .checkbox-container p {
    font-size: 10px;
  }
}
@media (max-width: 510px) {
  .contact-form h3 {
    width: 60%;
    font-size: 12px;
  }
}
@media (max-width: 450px) {
  .contact-form h1 {
    font-size: 24px;
  }
  .contact-form h2 {
    font-size: 16px;
  }
  .contact-form h3 {
    font-size: 12px;
  }
  .contact-form .application-btn {
    width: 60%;
  }
  .questions {
    padding: 0;
  }
  .questions .question {
    margin-left: 0;
  }
}