/* REST vars */
.banner-container {
  min-height: 14vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffd8c4;
}
.banner-container p {
  font-size: 28px;
  font-weight: 100;
  line-height: 1.5;
  text-align: center;
  color: #222222;
  letter-spacing: 0.8px;
}
.banner-container p span {
  font-weight: 100;
  color: #ffa574;
  font-family: "Rubik Gemstones", cursive;
  filter: drop-shadow(2px 2px #222222);
  text-transform: uppercase;
}

@media (max-width: 1550px) {
  .banner-container p {
    font-size: 25px;
  }
}
@media (max-width: 1290px) {
  .banner-container {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .banner-container p {
    width: 50%;
    font-size: 20px;
    line-height: 1.6;
  }
}
@media (max-width: 1180px) {
  .banner-container p {
    width: 50%;
  }
  .banner-container p span {
    filter: drop-shadow(1px 1px #302939);
  }
}
@media (max-width: 985px) {
  .banner-container p {
    width: 60%;
  }
}
@media (max-width: 820px) {
  .banner-container p {
    width: 70%;
    font-size: 18px;
  }
}
@media (max-width: 640px) {
  .banner-container p {
    width: 80%;
  }
}
@media (max-width: 570px) {
  .banner-container p {
    font-size: 16px;
  }
}