/* REST vars */
.landing-container {
  position: relative;
}
.landing-container .landing-content {
  width: 80%;
  min-height: 90vh;
  display: flex;
  gap: 40px;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
}
.landing-container .landing-content .landing-title {
  position: relative;
  display: flex;
  align-items: left;
  flex-direction: column;
  width: 80%;
  font-weight: 400;
  font-size: 60px;
  line-height: 1.7;
  text-align: left;
  letter-spacing: 1px;
  font-family: "Rubik Gemstones", cursive;
  text-transform: uppercase;
}
.landing-container .landing-content .landing-title .part-1 {
  color: #dda7bb;
  filter: drop-shadow(4px 4px #a8476a);
}
.landing-container .landing-content .landing-title .part-2 {
  color: #ffd8c4;
  filter: drop-shadow(4px 4px #f1702d);
}
.landing-container .landing-content .landing-btn {
  width: 25%;
}
.landing-container .landing-background-image {
  position: absolute;
  top: 5%;
  right: 5%;
  width: 35%;
  filter: drop-shadow(4px 4px #ffa574);
  animation: upAndDown 5s ease-out infinite;
}
.landing-container .landing-images {
  position: absolute;
  z-index: -4;
  color: #ffd8c4;
  font-size: 130px;
  filter: drop-shadow(4px 4px #ffa574);
}
.landing-container .landing-plane-image {
  top: 15%;
  left: 5%;
  transform: rotate(20deg);
}
.landing-container .landing-iceCreme-image {
  top: 15%;
  left: 45%;
  transform: rotate(-30deg);
}
.landing-container .landing-balloon-image {
  top: 73%;
  left: 4%;
  transform: rotate(30deg);
}
.landing-container .landing-cloud-image {
  top: 63%;
  left: 42%;
}
.landing-container .landing-spiral-image {
  top: 75%;
  left: 28%;
  font-size: 50px;
  opacity: 0.4;
}
.landing-container .landing-wave-image {
  top: 15%;
  left: 35%;
  font-size: 50px;
  opacity: 0.4;
}

@keyframes upAndDown {
  0% {
    top: 5%;
  }
  50% {
    top: 0%;
  }
  100% {
    top: 5%;
  }
}
@media (max-width: 1720px) {
  .landing-container .landing-content .landing-title {
