/* REST vars */
.menu-container {
  margin: 0;
}
.menu-container .menu-icon {
  color: #ffd8c4;
  line-height: 8vh;
  padding-left: 2rem;
  padding-top: 0.5rem;
  font-size: 50px;
  filter: drop-shadow(2px 2px #f1702d);
  cursor: pointer;
}
.menu-container .exit-icon {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 5%;
  left: 5%;
  color: #f1702d;
  font-size: 30px;
  background-color: transparent;
  filter: drop-shadow(10px 10px transparent);
  border: none;
  cursor: pointer;
}
.menu-container .small-menu {
  inset: 0;
  width: 50vw;
  height: 25vh;
  position: absolute;
  top: calc(15vh + 5px);
  left: 5px;
  z-index: -1;
  display: flex;
  gap: 20px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 35px;
  background-color: #302939;
  transition: 0.5s;
  opacity: 0;
}
.menu-container .small-menu .small-menu-content {
  width: 40vw;
  height: 4vh;
  padding-left: 1rem;
  display: flex;
  gap: 5px;
  align-items: center;
  background-color: #dda7bb;
  border-radius: 25px;
  filter: drop-shadow(2px 2px #a8476a);
  transition: 0.5s;
  cursor: pointer;
}
.menu-container .small-menu .small-menu-content .small-menu-icon {
  font-size: 20px;
  color: #fefcfd;
  filter: drop-shadow(1px 1px #222222);
}
.menu-container .small-menu .small-menu-content p {
  color: #fefcfd;
  font-size: 16px;
  font-weight: 500;
  font-family: "Bitter", sans-serif;
  letter-spacing: 2px;
  filter: drop-shadow(1px 1px #222222);
  text-transform: uppercase;
  transition: 0.5s;
}
.menu-container .small-menu-content:hover {
  transform: scale(0.95);
}
.menu-container .small-menu-move {
  opacity: 1;
  z-index: 100;
}

@media (max-width: 695px) {
  .menu-container .small-menu .small-menu-content p {
    font-size: 14px;
  }
}
@media (max-width: 395px) {
  .menu-container .small-menu .small-menu-content p {
    font-size: 12px;
  }
}