.projects-container {
  width: 50%;
  display: flex;
  gap: 80px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10rem auto;
}
.projects-container h1 {
  width: 80%;
  text-align: center;
  font-weight: 100;
  font-size: 55px;
  font-family: "Rubik Gemstones", cursive;
  letter-spacing: 1px;
  line-height: 1.8;
  margin: 0 auto;
}
.projects-container h1 span:nth-child(1) {
  color: #dda7bb;
  filter: drop-shadow(3px 3px #a8476a);
}
.projects-container h1 span:nth-child(2) {
  color: #ffd8c4;
  filter: drop-shadow(3px 3px #f1702d);
}
.projects-container h2 {
  width: 80%;
  font-weight: 100;
  font-size: 30px;
  font-family: "Bitter", sans-serif;
  letter-spacing: 2px;
  line-height: 1.6;
  text-align: center;
}
.projects-container .main-description {
  font-size: 18px;
  font-weight: 100;
  line-height: 2.4;
  letter-spacing: 1px;
  text-align: justify;
  margin: 0 auto;
}
.projects-container .main-description span {
  font-weight: 900;
}
.projects-container .projects {
  width: 100%;
  display: flex;
  gap: 80px;
  flex-direction: column;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
}
.projects-container .projects .project {
  width: 100%;
  display: flex;
  gap: 60px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.projects-container .projects .project .project-title {
  width: 50%;
  transition: 0.5s;
  cursor: pointer;
}
.projects-container .projects .project .project-title a {
  text-align: center;
  font-size: 30px;
  font-weight: 800;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffd8c4;
  filter: drop-shadow(3px 3px #f1702d);
}
.projects-container .projects .project .project-title:hover {
  transform: scale(0.9);
}
.projects-container .projects .project .project-image {
  width: 100%;
  border: 10px solid #fffbf8;
  border-radius: 25px;
  box-shadow: 2px 2px 5px #f1702d;
  filter: drop-shadow(10px 10px #ffd8c4);
}
.projects-container .projects .project .project-description {
  width: 100%;
  display: flex;
  gap: 40px;
  flex-direction: column;
  text-align: justify;
}
.projects-container .projects .project .project-description .points-title {
  text-align: center;
  font-size: 25px;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #dda7bb;
  font-family: "Bitter", sans-serif;
  filter: drop-shadow(2px 2px #a8476a);
}
.projects-container .projects .project .project-description h3 {
  font-size: 16px;
  font-weight: 300;
  line-height: 2.4;
  letter-spacing: 0.5px;
}
.projects-container .projects .project .project-description ul {
  display: flex;
  gap: 20px;
  flex-direction: column;
  list-style-type: none;
}
.projects-container .projects .project .project-description ul li {
  font-size: 16px;
  font-weight: 300;
  line-height: 2.4;
  letter-spacing: 0.5px;
}
.projects-container .projects .project .project-description ul li span {
  font-weight: 800;
  font-size: 18px;
  font-family: "Bitter", sans-serif;
  text-transform: uppercase;
}
.projects-container .projects .project .project-description .technologies {
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: center;
}
.projects-container .projects .project .project-description .technologies .technologie {
  display: flex;
  gap: 10px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 50px;
  color: #222222;
  transition: 0.5s;
  animation: scale 2.5s infinite linear;
  opacity: 0.5;
}
.projects-container .projects .project .project-description .technologies .technologie:hover {
  opacity: 1;
  color: #dda7bb;
}
.projects-container .projects .project .project-description .technologies .technologie p {
  text-align: center;
  opacity: 0;
  font-size: 12px;
  transition: 0.5s;
}
.projects-container .projects .project .project-description .technologies .technologie:hover p {
  opacity: 1;
}
.projects-container .projects .project .project-description .link-content {
  display: flex;
  gap: 5px;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
}
.projects-container .projects .project .project-description .link-content:hover {
  transform: scale(0.9);
}
.projects-container .projects .project .project-description .link-content .link {
  font-weight: 700;
  font-size: 16px;
  text-decoration-style: wavy;
  color: #222222;
  text-align: center;
  line-height: 2;
}
.projects-container .projects .project .project-description .link-content .link-icon {
  filter: none;
  color: #dda7bb;
  font-size: 25px;
  background-color: transparent;
  filter: drop-shadow(2px 2px #a8476a);
  animation: scale 2s infinite;
}

.project-images {
  position: absolute;
  z-index: -3;
  opacity: 0.3;
  color: #ffa574;
  font-size: 100px;
}

