}

.project-tree-image {
  top: 300%;
  left: 20%;
}

.project-sun-image {
  top: 320%;
  right: 10%;
  transform: rotate(10deg);
}

.project-smiley-image {
  top: 550%;
  right: 15%;
}

.project-paw-image {
  top: 575%;
  left: 10%;
