}

.project-star-image {
  top: 185%;
  right: 10%;
}

.project-line-image {
  top: 384%;
  right: 13%;
}

.project-plant-image {
  top: 485%;
  right: 20%;
}

.project-moon-image {
  top: 430%;
  left: 14%;
  transform: rotate(-10deg);
}

.project-balloon-image {
  top: 85%;
  left: 12%;
}

.project-cactus-image {
  top: 275%;
  right: 12%;
}

.project-palette-image {
  top: 475%;
  left: 20%;
}

@media (max-width: 1850px) {
  .projects-container h1 {
    width: 90%;
  }
}
@media (max-width: 1650px) {
  .projects-container h1 {
    width: 100%;
  }
  .projects-container h2 {
    width: 100%;
  }
}
@media (max-width: 1560px) {
  .projects-container h1 {
    font-size: 45px;
  }
  .projects-container h2 {
    font-size: 26px;
  }
  .projects-container .main-description {
    font-size: 16px;
  }
  .projects-container .projects .project {
    gap: 50px;
  }
  .projects-container .projects .project .project-title {
    width: 60%;
  }
  .projects-container .projects .project .project-title a {
    font-size: 26px;
  }
  .projects-container .projects .project .project-title:hover {
    transform: scale(0.9);
  }
  .projects-container .projects .project .project-description {
    gap: 40px;
  }
  .projects-container .projects .project .project-description .points-title {
    font-size: 20px;
  }
  .projects-container .projects .project .project-description h3 {
    font-size: 14px;
  }
  .projects-container .projects .project .project-description ul li {
    font-size: 14px;
  }
  .projects-container .projects .project .project-description ul li span {
    font-size: 16px;
  }
}
@media (max-width: 1290px) {
  .projects-container h1 {
    font-size: 40px;
  }
  .projects-container h2 {
    font-size: 24px;
  }
  .projects-container .main-description {
    font-size: 14px;
  }
  .projects-container .projects {
    gap: 50px;
  }
  .projects-container .projects .project {
    gap: 40px;
  }
  .projects-container .projects .project .project-title {
    width: 70%;
  }
}
@media (max-width: 1290px) {
  .projects-container {
    width: 55%;
    gap: 40px;
  }
  .projects-container h1 {
    font-size: 35px;
  }
  .projects-container .projects .project .project-description .technologies {
    display: grid;
    grid-template-columns: 2fr 2fr 2fr 2fr;
    gap: 5px;
  }
  .project-palette-image {
    top: 425%;
    left: 20%;
  }
}
@media (max-width: 950px) {
  .projects-container {
    width: 60%;
  }
}
@media (max-width: 850px) {
  .projects-container {
    width: 65%;
  }
}
@media (max-width: 780px) {
  .projects-container h1 {
    font-size: 35px;
  }
  .projects-container h1 span:nth-child(1) {
    filter: drop-shadow(1.5px 1.5px #a8476a);
  }
  .projects-container h1 span:nth-child(2) {
    filter: drop-shadow(1.5px 1.5px #f1702d);
  }
  .projects-container h2 {
    font-size: 22px;
  }
  .projects-container .main-description {
    font-size: 12px;
  }
  .projects-container .projects {
    gap: 40px;
  }
  .projects-container .projects .project {
    gap: 30px;
  }
  .projects-container .projects .project .project-title {
    width: 70%;
  }
  .projects-container .projects .project .project-title a {
    font-size: 22px;
    filter: drop-shadow(1.5px 1.5px #f1702d);
  }
  .projects-container .projects .project .project-image {
    border: 5px solid #fffbf8;
  }
  .projects-container .projects .project .project-description {
    gap: 25px;
  }
  .projects-container .projects .project .project-description .points-title {
    font-size: 18px;
    filter: drop-shadow(1.5px 1.5px #a8476a);
  }
  .projects-container .projects .project .project-description h3 {
    font-size: 12px;
  }
  .projects-container .projects .project .project-description ul li {
    font-size: 12px;
  }
  .projects-container .projects .project .project-description ul li span {
    font-size: 14px;
  }
  .projects-container .projects .project .project-description .technologies .technologie {
    font-size: 35px;
  }
  .projects-container .projects .project .project-description .technologies .technologie p {
    font-size: 10px;
  }
  .projects-container .projects .project .project-description .link-content .link {
    font-size: 14px;
  }
  .project-smiley-image {
    top: 450%;
    right: 15%;
  }
  .project-paw-image {
    top: 475%;
    left: 10%;
    transform: rotate(-20deg);
  }
}
@media (max-width: 680px) {
  .projects-container {
    width: 70%;
  }
  .projects-container h1 {
    font-size: 30px;
  }
}
@media (max-width: 550px) {
  .projects-container {
    width: 90%;
  }
  .projects-container h1 {
    font-size: 26px;
  }
  .projects-container h2 {
    font-size: 18px;
  }
}
@media (max-width: 420px) {
  .projects-container h1 {
    font-size: 22px;
  }
  .projects-container h2 {
    font-size: 16px;
  }
  .projects-container .projects .project .project-title {
    width: 80%;
  }
  .projects-container .projects .project .project-title a {
    font-size: 20px;
  }
  .projects-container .projects .project .project-description {
    gap: 24px;
  }
  .projects-container .projects .project .project-description .points-title {
    font-size: 16px;
  }
  .projects-container .projects .project .project-description .technologies {
    gap: 0;
  }
  .projects-container .projects .project .project-description .technologies .technologie {
    font-size: 28px;
  }
  .projects-container .projects .project .project-description .technologies .technologie p {
    font-size: 8px;
  }
  .projects-container .projects .project .project-description .link-content .link {
    font-size: 12px;
  }
}