/* REST vars */
.menu-container {
  display: grid;
  grid-template-columns: 0.85fr 0.15fr;
  box-shadow: 4px 4px 6px #fffbf8;
  background-color: #302939;
}
.menu-container .menu {
  min-height: 8vh;
  display: flex;
  gap: 40px;
  align-items: center;
  padding-left: 6rem;
}
.menu-container .menu .menu-element {
  color: #ffd8c4;
  filter: drop-shadow(2px 2px #f1702d);
  font-size: 30px;
  font-weight: 500;
  font-family: "Rubik Gemstones", cursive;
  letter-spacing: 3px;
  text-transform: uppercase;
  border: 1px solid transparent;
  text-align: center;
  transition: 0.5s;
  cursor: pointer;
}
.menu-container .menu .menu-element:hover {
  color: #dda7bb;
  transform: scale(0.85);
  filter: drop-shadow(2px 2px #a8476a);
}

.menu-box {
  display: none;
}

@media (max-width: 1615px) {
  .menu-container {
    grid-template-columns: 0.85fr 0.15fr;
  }
}
@media (max-width: 1315px) {
  .menu-container {
    grid-template-columns: 0.95fr 0.05fr;
  }
  .menu-container .menu {
    padding-left: 5rem;
  }
}
@media (max-width: 980px) {
  .menu-container .menu {
    gap: 20px;
    padding-left: 2rem;
  }
  .menu-container .menu .menu-element {
    margin: 1rem 0;
    padding: 0 0.5rem;
    font-size: 28px;
  }
}
@media (max-width: 875px) {
  .menu-container {
    margin: 2rem;
  }
  .menu-container .menu {
    display: none;
  }
  .menu-container .menu-box {
    display: block;
  }
  .menu-container .menu-box .menu-icon {
    font-size: 40px;
    border: none;
    color: #ffd8c4;
    background-color: transparent;
    filter: drop-shadow(1px 1px #f1702d);
  }
}