/* REST vars */
.background-values-container {
  background-color: #302939;
}
.background-values-container .values-container {
  position: relative;
  margin: 4rem auto 0;
  width: 60%;
  display: flex;
  gap: 60px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fffbf8;
}
.background-values-container .values-container h1 {
  text-align: center;
  font-weight: 100;
  color: #dda7bb;
  font-size: 40px;
  font-family: "Rubik Gemstones", cursive;
  line-height: 1.1111111111;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  filter: drop-shadow(3px 3px #a8476a);
  margin: 0 auto;
}
.background-values-container .values-container h2 {
  font-weight: 100;
  font-size: 30px;
  color: #dda7bb;
  letter-spacing: 1px;
}
.background-values-container .values-container h3 {
  width: 90%;
  font-weight: 400;
  font-size: 16px;
  letter-spacing: 1px;
  line-height: 2.2;
  text-align: justify;
}
.background-values-container .values-container .values {
  width: 80%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  place-content: center;
  gap: 80px;
}
.background-values-container .values-container .values .value {
  margin-top: 1.5rem;
  text-align: center;
  letter-spacing: 1px;
}
.background-values-container .values-container .values .value:hover .value-icon {
  transform: scale(0.95);
  opacity: 1;
}
.background-values-container .values-container .values .value.active .value-icon {
  opacity: 1;
}
.background-values-container .values-container .values .value-icon {
  font-size: 100px;
  background-color: transparent;
  border: none;
  color: #fffbf8;
  filter: drop-shadow(2px 2px #a8476a);
  transition: 0.5s;
  opacity: 0.3;
  cursor: pointer;
}
.background-values-container .values-description .value-content {
  display: flex;
  gap: 20px;
  min-height: 30vh;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: -3rem;
}
.background-values-container .values-description .value-content h2 {
  color: #dda7bb;
  font-size: 30px;
  font-family: "Rubik Gemstones", cursive;
  font-weight: 100;
  text-align: center;
  filter: drop-shadow(2px 2px #a8476a);
  text-transform: uppercase;
  letter-spacing: 2px;
}
.background-values-container .values-description .value-content .value-text {
  display: grid;
  grid-template-columns: 0.5fr 4fr 0.5fr;
  gap: 20px;
}
.background-values-container .values-description .value-content .value-text p {
  color: #fffbf8;
  font-weight: 100;
  font-size: 16px;
  line-height: 2.4;
  letter-spacing: 1px;
  text-align: center;
}
.background-values-container .values-description .value-content .value-text p span {
  font-weight: 800;
  color: #dda7bb;
  filter: drop-shadow(1px 1px #a8476a);
}
.background-values-container .values-description .value-content .value-text .values-btn {
  filter: none;
  color: #fffbf8;
  font-size: 40px;
  background-color: transparent;
  filter: drop-shadow(2px 2px #a8476a);
  animation: scale 2s infinite;
}

.values-images {
  position: absolute;
  opacity: 0.3;
  color: #dda7bb;
  font-size: 100px;
}

.values-flower-image {
  top: -8%;
  right: 0%;
  transform: rotate(-20deg);
}

.values-heart-image {
  top: 25%;
  left: -15%;
  transform: rotate(-10deg);
}

.values-brush-image {
  top: 78%;
  right: -5%;
}

.values-butterfly-image {
  top: 85%;
  left: 10%;
  transform: rotate(30deg);
}

@media (max-width: 1680px) {
  .background-values-container .values-container {
    width: 65%;
  }
}
@media (max-width: 1385px) {
  .background-values-container .values-container .values .value-icon {
    font-size: 80px;
  }
}
@media (max-width: 1270px) {
  .background-values-container .values-container h1 {
    font-size: 35px;
  }
  .background-values-container .values-container h2 {
    font-size: 28px;
  }
}
@media (max-width: 1095px) {
  .background-values-container .values-container h2 {
    width: 70%;
    text-align: center;
    line-height: 1.6;
  }
}
@media (max-width: 985px) {
  .background-values-container .values-container h1 {
    font-size: 30px;
    line-height: 2;
    filter: drop-shadow(1px 1px #a8476a);
  }
  .background-values-container .values-container h2 {
    font-size: 24px;
  }
  .background-values-container .values-description .value-content .value-text p {
    font-size: 14px;
  }
  .background-values-container .values-description .value-content .value-text p span {
    filter: drop-shadow(1px 1px #a8476a);
  }
  .values-flower-image {
    top: 0%;
    right: 3%;
  }
  .values-brush-image {
    top: 88%;
    right: 5%;
  }
  .values-butterfly-image {
    top: 87%;
  }
}
@media (max-width: 790px) {
  .background-values-container .values-container h2 {
    filter: drop-shadow(1px 1px #a8476a);
  }
  .background-values-container .values-container h3 {
    font-size: 14px;
  }
  .background-values-container .values-container .values {
    width: 50%;
    gap: 20px;
    grid-template-columns: 2fr 2fr;
  }
  .background-values-container .values-container .values .value-icon {
    font-size: 90px;
  }
  .background-values-container .values-description .value-content .value-text {
    grid-template-columns: 0.5fr 6fr 0.5fr;
  }
  .background-values-container .values-description .value-content .value-text .values-btn {
    padding: 0;
    font-size: 25px;
  }
  .values-images {
    font-size: 80px;
  }
  .values-heart-image {
    top: 5%;
  }
}
@media (max-width: 680px) {
  .background-values-container .values-container {
    width: 70%;
  }
  .background-values-container .values-description .value-content .value-text {
    display: flex;
    gap: 20px;
  }
  .values-flower-image {
    top: -3%;
  }
  .values-heart-image {
    top: 3%;
  }
  .values-brush-image {
    top: 90%;
  }
  .values-butterfly-image {
    top: 90%;
  }
}
@media (max-width: 600px) {
  .background-values-container .values-container {
    width: 90%;
  }
  .background-values-container .values-container h3 {
    width: 100%;
  }
  .background-values-container .values-description .value-content {
    margin-top: -1rem;
  }
  .background-values-container .values-description .value-content h2 {
    width: 100%;
  }
  .background-values-container .values-description .value-content .value-text {
    grid-template-columns: 0fr 6fr 0fr;
  }
  .background-values-container .values-description .value-content .value-text .values-btn {
    display: none;
  }
}
@media (max-width: 470px) {
  .background-values-container .values-description .values-container {
    gap: 30px;
  }
  .background-values-container .values-description .values-container h1 {
    text-align: center;
    font-size: 21px;
  }
  .background-values-container .values-description .values-container h2 {
    width: 100%;
  }
  .background-values-container .values-description .values-container h3 {
    font-size: 13px;
  }
  .values-butterfly-image {
    top: 92%;
  }
}