/* REST vars */
.contact-icon-container {
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: center;
}
.contact-icon-container .contact-icon {
  z-index: 100;
  font-size: 50px;
  color: #ffd8c4;
  filter: drop-shadow(2px 2px #f1702d);
  transition: 0.5s;
  cursor: pointer;
}
.contact-icon-container .contact-icon:hover {
  color: #dda7bb;
  filter: drop-shadow(2px 2px #a8476a);
  transform: scale(0.9);
}

@media (max-width: 980px) {
  .contact-icon-container {
    gap: 7px;
  }
  .contact-icon-container .contact-icon {
    font-size: 50px;
  }
}
@media (max-width: 875px) {
  .contact-icon-container {
    gap: 5px;
  }
  .contact-icon-container .contact-icon {
    font-size: 45px;
    filter: drop-shadow(1.5px 1.5px #f1702d);
  }
}