/* REST vars */
.topBanner-container {
  min-height: 6vh;
  display: flex;
  gap: 50px;
  align-items: center;
  justify-content: center;
  color: #fffbf8;
  background-color: #302939;
}
.topBanner-container .slogan {
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: center;
}
.topBanner-container .slogan p {
  font-size: 16px;
  text-align: center;
  font-weight: 100;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  line-height: 1.8;
}
.topBanner-container .top-banner-icon {
  font-size: 25px;
}
.topBanner-container .call-to-action-button {
  letter-spacing: 1px;
  animation: scale 3s infinite ease-in-out;
  filter: drop-shadow(2px 2px #a8476a);
  transition: 0.5s;
  cursor: pointer;
}
.topBanner-container .call-to-action-button p {
  font-size: 15px;
}

@media (max-width: 1450px) {
  .topBanner-container .call-to-action-button {
    padding: 0.55rem;
  }
  .topBanner-container .call-to-action-button p {
    font-size: 13px;
  }
  .topBanner-container .top-banner-icon {
    font-size: 30px;
  }
}
@media (max-width: 1250px) {
  .topBanner-container .slogan {
    flex-direction: column;
    gap: 2.5px;
  }
  .topBanner-container .slogan p {
    font-size: 13px;
  }
  .topBanner-container .top-banner-icon {
    display: none;
  }
}
@media (max-width: 850px) {
  .topBanner-container {
    gap: 20px;
  }
}
@media (max-width: 700px) {
  .topBanner-container .slogan p {
    font-size: 13px;
  }
  .topBanner-container .call-to-action-button {
    padding: 0.5rem;
  }
  .topBanner-container .call-to-action-button p {
    font-size: 12px;
  }
}
@media (max-width: 640px) {
  .topBanner-container .slogan p {
    font-size: 11px;
    filter: none;
  }
  .topBanner-container .call-to-action-button {
    width: 30%;
  }
}
@media (max-width: 550px) {
  .topBanner-container {
    gap: 10px;
  }
  .topBanner-container .slogan {
    gap: 0;
  }
  .topBanner-container .slogan p {
    font-size: 10px;
  }
  .topBanner-container .call-to-action-button {
    width: 30%;
    margin-top: 0.25rem;
  }
  .topBanner-container .call-to-action-button p {
    font-size: 9px;
  }
}
@media (max-width: 450px) {
  .topBanner-container {
    gap: 2px;
  }
  .topBanner-container .slogan {
    gap: 0.5px;
  }
  .topBanner-container .slogan p {
    font-size: 8px;
  }
  .topBanner-container .call-to-action-button {
    padding: 0.25rem;
  }
  .topBanner-container .call-to-action-button p {
    font-size: 8px;
  }
}