/* REST vars */
.background-registration-container {
  position: relative;
  background-color: #302939;
}
.background-registration-container .registration-container {
  margin-top: 2rem;
  display: grid;
  gap: 60px;
  grid-template-rows: 0.05fr 0.95fr;
  color: #fffbf8;
}
.background-registration-container .registration-container h1 {
  width: 60%;
  text-align: center;
  font-weight: 100;
  color: #fffbf8;
  font-size: 44px;
  font-family: "Rubik Gemstones", cursive;
  line-height: 1.8;
  letter-spacing: 2px;
  filter: drop-shadow(3px 3px #a8476a);
  margin: 0 auto;
}
.background-registration-container .registration-container .steps-container {
  width: 70%;
  margin: 0 auto 8rem;
  display: flex;
  gap: 60px;
  align-items: center;
  flex-direction: column;
}
.background-registration-container .registration-container .steps-container .step {
  position: relative;
  width: 65%;
  justify-self: center;
}
.background-registration-container .registration-container .steps-container .step h2 {
  font-size: 30px;
  font-weight: 400;
  font-family: "Bitter", sans-serif;
  line-height: 1.5;
  letter-spacing: 3px;
  text-transform: uppercase;
  color: #fffbf8;
  margin: 1rem 0;
}
.background-registration-container .registration-container .steps-container .step p {
  font-weight: 100;
  font-size: 20px;
  letter-spacing: 1px;
  line-height: 2;
  text-align: justify;
  color: #fffbf8;
  padding: 0;
}
.background-registration-container .registration-container .steps-container .step span {
  font-weight: 900;
  font-size: 25px;
  color: #dda7bb;
  filter: drop-shadow(1px 1px #a8476a);
}

.registration-images {
  position: absolute;
  z-index: 1;
  opacity: 0.3;
  color: #dda7bb;
  font-size: 120px;
}

.registration-star-image {
  top: 0%;
  left: 15%;
  transform: rotate(5deg);
}

.registration-rainbow-image {
  top: 30%;
  right: 10%;
}

.registration-rocket-image {
  top: 55%;
  left: 10%;
}

.registration-sparkle-image {
  top: 80%;
  right: 15%;
}

.registration-shapes-image {
  top: 52%;
  right: 35%;
}

@media (max-width: 1680px) {
  .background-registration-container .registration-container {
    gap: 55px;
  }
  .background-registration-container .registration-container h1 {
