/* REST vars */
.footer-content {
  position: relative;
  width: 100%;
  background-color: #fff2eb;
}
.footer-content .copyright-text {
  margin: 0 auto;
  font-weight: 500;
  text-align: center;
  line-height: 4;
  font-size: 14px;
}
.footer-content hr {
  width: 40%;
  margin: 0 auto;
  height: 4px;
  border: 4px solid #dda7bb;
  border-radius: 25px;
  filter: drop-shadow(4px 4px #a8476a);
}
.footer-content .footer-text {
  width: 70%;
  margin: 0 auto;
  display: grid;
  justify-content: center;
  gap: 20px;
  grid-template-columns: 0.3fr 0.3fr 0.4fr;
  padding: 5rem 0;
}
.footer-content .footer-text .footer-text-element {
  width: 100%;
  display: flex;
  gap: 12px;
  flex-direction: column;
}
.footer-content .footer-text .footer-text-element h2 {
  color: #dda7bb;
  font-size: 30px;
  font-weight: 900;
  font-family: "Bitter", sans-serif;
  line-height: 2;
  letter-spacing: 2px;
  text-transform: uppercase;
  filter: drop-shadow(2px 2px #a8476a);
  text-align: center;
}
.footer-content .footer-text .footer-text-element p {
  line-height: 1.8;
  font-size: 18px;
  font-weight: 600;
  text-align: center;
}
.footer-content .footer-text .footer-text-element .footer-slogan {
  font-weight: 600;
  font-size: 20px;
}
.footer-content #footer-menu-btn {
  width: 80%;
  margin: 0 auto;
  border: none;
  background-color: #ffd8c4;
  border-radius: 25px;
  transition: 0.5s;
  cursor: pointer;
}
.footer-content #footer-menu-btn p {
  display: flex;
  gap: 5px;
  align-items: center;
  padding: 0 0 0 1rem;
  color: #222222;
  font-size: 18px;
  font-family: "Comfortaa", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.footer-content #footer-menu-btn:hover {
  transform: scale(0.95);
}
.footer-content .policy-link {
  color: #ffa574;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 600;
  transition: 0.5s;
  filter: drop-shadow(1px 1px #f1702d);
  cursor: pointer;
}
.footer-content .policy-link:hover {
  font-size: 12px;
}

@media (max-width: 1285px) {
  .footer-content .footer-text {
    width: 80%;
  }
}
@media (max-width: 1185px) {
  .footer-content .footer-text {
    width: 90%;
  }
}
@media (max-width: 1005px) {
  .footer-content .footer-text {
    grid-template-columns: 0.3fr 0.3fr 0.4fr;
  }
  .footer-content .footer-text .footer-text-element {
    gap: 10px;
  }
  .footer-content .footer-text .footer-text-element h2 {
    font-size: 25px;
    filter: drop-shadow(2px 2px #a8476a);
  }
  .footer-content .footer-text .footer-text-element p {
    font-size: 16px;
  }
  .footer-content #footer-menu-btn p {
    font-size: 16px;
  }
}
@media (max-width: 925px) {
  .footer-content hr {
    width: 60%;
  }
  .footer-content .footer-text {
    width: 60%;
    display: flex;
    gap: 30px;
    flex-direction: column;
    padding: 3rem 0;
  }
}
@media (max-width: 770px) {
  .footer-content .copyright-text {
    font-size: 12px;
  }
  .footer-content .footer-text {
    grid-template-columns: 0.33fr 0.33fr 0.33fr;
  }
  .footer-content .footer-text .footer-text-element h2 {
    font-size: 20px;
    filter: drop-shadow(1.5px 1.5px #a8476a);
  }
  .footer-content .footer-text .footer-text-element p {
    font-size: 14px;
  }
  .footer-content #footer-menu-btn p {
    font-size: 14px;
  }
}
@media (max-width: 700px) {
  .footer-content .footer-text {
    gap: 30px;
  }
  .footer-content .footer-text .footer-text-element h2 {
    font-size: 20px;
  }
}
@media (max-width: 550px) {
  .footer-content .copyright-text {
    font-size: 9px;
  }
}
@media (max-width: 465px) {
  .footer-content .footer-text {
    width: 70%;
  }
}
@media (max-width: 375px) {
  .footer-content p {
    font-size: 12px;
  }
  .footer-content .footer-text {
    width: 80%;
  }
  .footer-content .footer-text .footer-text-element h2 {
    font-size: 18px;
  }
  .footer-content .footer-text .footer-text-element p {
    font-size: 12px;
  }
  .footer-content #footer-menu-btn p {
    font-size: 12px;
  }
}